const Characters = () => {

  return (

    <div className="characters-app">
      
      <ParallaxBanner
        layers={[
          {
            image: characters,
            amount: 0.15,
            speed: -20,
          },
        ]}
        style={{
            width: '100%',
            height: '40vw',
            position: "relative",
        }}
      >

        <div className="characters-container">
          <p className="characters-text-title"> Collect your favorite Human from the manga... </p>
        </div> 

      </ParallaxBanner>

      <div className="characters-char">

        {listHuman.map((list) => (
          <div className="characters-char-element" style={{ top: list.top, left: list.left }}>
            <Parallax y={[list.y1, list.y2]} x={[list.x1, list.x2]} className="characters-char-container">
              <p alt="" className="characters-char-name" > 
                {list.name} 
                <div className="characters-char-img-container">
                  <img alt="" className="characters-char-img" src={list.img} />
                </div>
              </p>
            </Parallax> 
          </div>
        ))}

      </div> 

      <ParallaxBanner
        layers={[
          {
              image: whales,
              amount: 0.1,
          },
        ]}
        style={{
          width: '100%',
          height: '50vw',
          position: "relative",
        }}
      >

        <div className="characters-container">
          <p className="characters-text-title"> ...And be part of the fight against the whales... </p>
        </div> 

      </ParallaxBanner>

      <div className="characters-char">

        {listWhales.map((list) => (
          <div className="characters-char-element" style={{ top: list.top, left: list.left }}>
            <Parallax y={[list.y1, list.y2]} x={[list.x1, list.x2]} className="characters-char-container">
              <p alt="" className="characters-char-name" > 
                {list.name} 
                <div className="characters-char-img-container">
                  <img alt="" className="characters-char-img" src={list.img} />
                </div>
              </p>
            </Parallax> 
          </div>
        ))}

      </div> 

    </div>

  );
};

export default (Characters);
