import React, { useState, useRef, useEffect, createContext } from 'react';

import bgSmicu from '../../assets/pictures/bg-smicu-gli.png';
import smicu from '../../assets/pictures/smicu-gli.png';
import taskLeft from '../../assets/pictures/updates-task-left.png';
import taskRight from '../../assets/pictures/updates-task-right.png';
import intro from '../../assets/intro.mp4';

import './style.css';



const listCard = [
  { id: 2, 
    img: smicu,
    video: 0,
    infos: '01/10/22 by Ous', 
    title: 'Discord release', 
    text: "Discord is now open, join us!",
    url: "https://discord.gg/kujiranokonton",
    urlText: "Go to discord!"
  },
  { id: 1, 
    img: smicu,
    video: intro,
    infos: '01/08/22 by Chris', 
    title: 'Teaser 1', 
    text: "The whales have sent their best assets to kill the humans. 4 new characters are now part of our NFT collection ! Watch until the end. We hope you enjoyed the reference..."
  },
  { id: 0, 
    img: smicu,
    video: 0,
    infos: '01/06/22 by Yogaa', 
    title: 'Introducing KNK', 
    text: "Our story takes place in a world, where humans have been defeated, and whales have taken over the world. Kujira No Konton is the first manga evolving on the blockchain thanks to NFTs. Manga and many other things in the making..."
  },
];

const Updates = () => {

  /* TOGGLE CLICK */
  const [selected, setSelected] = useState(2);

  const handleColor = (row) => {
    setSelected(row.id);
  };

  return (

    <div className="updates-app">

      <img alt="" className="updates-task-left" src={taskLeft} />
      <img alt="" className="updates-task-right" src={taskRight} />


      <div className="updates-container-title">

        <div className="title-container" style={{marginBottom: '3vw'}}>
          <p className="title-text"> What's new </p>
          <p className="title-slash"> / </p> 
          <p className="title-description"> 新着情報 </p> 
        </div>

      </div>


      <div className="updates-container-content">

        <div className="updates-container-left">
          {listCard.map((list) => (
            <div
              key={list.id}
              onClick={() => handleColor(list)}
              className={(list.id === selected) ? "updates-card-container updates-card-container-selected" : "updates-card-container" }
            >
              <img alt="" className="updates-card-img" src={list.img} />
              <div className="updates-card-content">
                <p className="updates-card-infos"> {list.infos} </p> 
                <p className="updates-card-title"> {list.title} </p>
              </div>
            </div>
          ))}
        </div>

        <div className="updates-container-right">
          {listCard.map((list) => (
            <div
              key={list.id}
              className={(list.id === selected) ? "updates-content-container" : "updates-content-hide" }
            >
              <p className="updates-content-infos"> {list.infos} </p>
              <p className="updates-content-title"> {list.title} </p>
              <p className="updates-content-text"> {list.text} </p>
              <div className="updates-content-url">
                <a className="updates-content-urlText" href={list.url} target="_blank" rel="noreferrer" > {list.urlText} </a>
              </div>
              <video className={ (list.video == 0) ? "hide": "updates-content-video"} src={list.video} controls="controls" autoplay={false} />
            </div>
          ))}
        </div>

      </div>


    </div>
  );
};

export default (Updates);
