const PawCat = () => {

  const [count, setCount] = useState(0);

  useEffect(() => {

    let counter = count;

    const interval = setInterval(() => {
      if (counter >= PawQ12.length+1) {
        setCount(0);
        counter = 0;
      } else {
        setCount(count => count + 1);
        counter++; // local variable that this closure will see
      }
    }, 500);

  }, []);

  let allPawList = PawQ12.slice(0, count).map((list) => {
    return (
      <img alt="" className="roadmap-paw" style={{transform: list.rotate, top: list.top, left: list.left, right: list.right }} src={paw} />
    )
  })

  return (
    <div>
      {allPawList}
    </div>
  )
}

const Roadmap = () => {

  return (

    <div className="roadmap-container">

      {listBgImg.map((list) => (
        <img alt="" className="roadmap-bg-img" style={{width: list.width, top: list.top, left: list.left, right: list.right, bottom: list.bottom }} src={list.img} />
      ))}

      <PawCat />

       {/*   {PawQ12.map((list) => (
        <img alt="" className="roadmap-paw" style={{transform: list.rotate, top: list.top, left: list.left, right: list.right }} src={paw} />
      ))}  */}

      <div className="roadmap-container-title">
        <div className="title-container" >
          <p className="title-text"> Roadmap </p>
          <p className="title-slash"> / </p> 
          <p className="title-description"> ロードマップ </p> 
        </div>
      </div>

      {listHouse.map((list) => (
        <div key={list.id} className="roadmap-house" style={{top: list.top, left: list.left, right: list.right }}>
          <img alt="" className="roadmap-house-img" src={house} />
          <p className="roadmap-house-quarter"> 
            {list.long}
            <p className="roadmap-house-quarter-short"> {list.short} </p> 
          </p>
          <div className="roadmap-house-description" style={{ filter: list.blur, webkitFilter: list.blur }}>
            {list.roadmap.map((list2) => (
              <p className="roadmap-house-description-text" > {list2.line} </p> 
            ))}
          </div>
        </div>
      ))}

    </div>
  );
};

