import React, { useRef } from 'react';
import useWindowDimensions from '../../utils/useWindowDimensions';

import bigmechant from '../../assets/Big-mechant.png';
import smicu from '../../assets/Smicu.png';
import daron from '../../assets/Daron.png';
import chn from '../../assets/chn.png';
import leftTask from '../../assets/left-task.png';
import rightTask from '../../assets/right-task.png';

import './index.css';


const RandomSign = (props) => {

  const { height, width } = useWindowDimensions();

  const ChipStyles = useRef({
      position: 'absolute',
      top: Math.floor(5+Math.random()*(height-100)),
      left: Math.floor(5+Math.random()*(width-100)),
      width: `${1+Math.floor(Math.random()*3)}vw`,
      animationDuration: `${props.duration}s`,
      webkitAnimationDuration: `${props.duration}s`,
  });

  return (
    <p className="home-text-random" style={ChipStyles.current}> {props.text} </p>
  )
}


const Home = () => {

  return (

    <div className="home-app">

      <img alt="" className="home-container-chn" src={chn} />

      <div className="home-container">

        <div className="home-container-left">
          <div className="home-container-titles-bottom">
            <p className="home-text-title-chn"> クジラの混沌 </p>
            <p className="home-text-title"> Kujira No Konton </p>
            <p className="home-text-description"> First manga evolving on the blockchain </p>
          </div>  
        </div>  

        <div className="home-container-right">
          <div className="home-character-Left-img">
            <img alt="" className="home-character-img" src={bigmechant} />
          </div>
          <div className="home-character-Center-img">
            <img alt="" className="home-character-img" src={smicu} />
          </div>
          <div className="home-character-Right-img">
            <img alt="" className="home-character-img" src={daron} />
          </div>
          <div className="home-character-Left-task">
            <img alt="" className="home-character-img" src={leftTask} />
          </div>
          <div className="home-character-Right-task">
            <img alt="" className="home-character-img" src={rightTask} />
          </div>
        </div>
        
      </div>

      <div className="home-red-line">
        <div>
          <p className="home-red-line-text"> Collection of 7,777 unique pieces </p>
        </div>
      </div>
      

      <RandomSign text={'い'} duration={10+Math.floor(Math.random()*10)}/>
      <RandomSign text={'え'} duration={10+Math.floor(Math.random()*10)}/>
      <RandomSign text={'そ'} duration={10+Math.floor(Math.random()*10)}/>
      <RandomSign text={'れ'} duration={10+Math.floor(Math.random()*10)}/>
      <RandomSign text={'は'} duration={10+Math.floor(Math.random()*10)}/>
      <RandomSign text={'て'} duration={10+Math.floor(Math.random()*10)}/>
      <RandomSign text={'が'} duration={7+Math.floor(Math.random()*10)}/>
      <RandomSign text={'る'} duration={7+Math.floor(Math.random()*10)}/>
      <RandomSign text={'こ'} duration={7+Math.floor(Math.random()*10)}/>
      <RandomSign text={'と'} duration={7+Math.floor(Math.random()*10)}/>
      <RandomSign text={'す'} duration={7+Math.floor(Math.random()*10)}/>
      <RandomSign text={'り'} duration={7+Math.floor(Math.random()*10)}/>
      <RandomSign text={'か'} duration={7+Math.floor(Math.random()*10)}/>
      <RandomSign text={'マ'} duration={3+Math.floor(Math.random()*10)}/>
      <RandomSign text={'ス'} duration={3+Math.floor(Math.random()*10)}/>
      <RandomSign text={'に'} duration={3+Math.floor(Math.random()*10)}/>
      <RandomSign text={'彼'} duration={3+Math.floor(Math.random()*10)}/>
      <RandomSign text={'た'} duration={3+Math.floor(Math.random()*10)}/>
      <RandomSign text={'は'} duration={3+Math.floor(Math.random()*10)}/>
      <RandomSign text={'て'} duration={3+Math.floor(Math.random()*10)}/>
      <RandomSign text={'い'} duration={12+Math.floor(Math.random()*10)}/>
      <RandomSign text={'し'} duration={12+Math.floor(Math.random()*10)}/>
      <RandomSign text={'ま'} duration={12+Math.floor(Math.random()*10)}/>
      <RandomSign text={'僧'} duration={12+Math.floor(Math.random()*10)}/>
      <RandomSign text={'侶'} duration={12+Math.floor(Math.random()*10)}/>
      <RandomSign text={'は'} duration={8+Math.floor(Math.random()*10)}/>
      <RandomSign text={'が'} duration={8+Math.floor(Math.random()*10)}/>
      <RandomSign text={'な'} duration={8+Math.floor(Math.random()*10)}/>
      <RandomSign text={'い'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'は'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'し'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'た'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'ま'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'は'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'心'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'が'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'は'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'も'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'、'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'も'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'風'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'-'} duration={5+Math.floor(Math.random()*10)}/>
      <RandomSign text={'旗'} duration={5+Math.floor(Math.random()*10)}/>
    </div>
  );
};

export default (Home);
