import './index.css';
import logo from '../../assets/pictures/logo.png';
import { Link } from "react-router-dom";

import React, { useState, useRef, useEffect, createContext } from 'react';
import { Transition } from 'react-transition-group';

import discord from '../../assets/svg/discord.svg';
import twitter from '../../assets/svg/twitter.svg';
import opensea from '../../assets/svg/opensea.svg';

import AudioPlayer from "../Player";
import tracks from "../Player/TracksList";

import Menu from '../Menu'

import { ethers } from 'ethers'
//import Modal from '../Modal'
import myEpicNft from '../../utils/MyEpicNFT.json'

const TOTAL_MINT_COUNT = 50;
const CONTRACT_ADDRESS = "0xC927f6b7DAc4B660349D596d219f6eA7B7C01B57";




const Navbar = () => {

  /* TOGGLE MENU ON PHONE */
  const [toggleMenu, setToggle] = useState(false)

  const toggleNavbar = () => {
    setToggle(currentValue => !currentValue);
  }
  
    return (

      <div>

        <div className="navbar-mobile">
          <div className="navbar-mobile-container">
            
            <Link to="/" style={{textDecoration: "none"}}>
              <div className="navbar-mobile-logo-container">
                <img alt="" className="navbar-mobile-logo-img" src={logo} />
                <div className="navbar-mobile-logo-container">
                  <p className="navbar-mobile-logo-text"> クジラの混沌 </p>
                </div>
              </div>
            </Link>
            
            <div className={`navbar-menu-icon ${toggleMenu ? "is-opened" : "is-closed"}`} onClick={() => setToggle(!toggleMenu)} >
              <span></span>
            </div>

          </div>
        </div>

        <Menu in={toggleMenu} toggleNavbar={toggleNavbar} />
    
      </div>
    );
  }


export default (Navbar);




