import React from 'react';

import bgSmicu from '../../assets/pictures/bg-smicu-gli.png';
import smicu from '../../assets/pictures/smicu-gli.png';
import aboutChn from '../../assets/about-chn.png';

import './style.css';


const About = () => {

  return (

    <div className="about-app">

        <div className="about-container">

          <div className="about-container-left">
            <div>
              <div className="title-container" style={{margin: '3% 0'}}>
                <p className="title-text"> About </p>
                <p className="title-slash"> / </p> 
                <p className="title-description"> 約 </p> 
              </div>
              <p className="about-text-description"> Kujira No Konton is the first manga evolving on the blockchain thanks to NFTs. </p>
              <p className="about-text-description"> Our story takes place in a world, where humans have been defeated, and whales have taken over the world... </p>
              <p className="about-text-description"> ...Buy at least one NFT to access publications in the "manga" section! </p>
            </div>
          </div>         

          <div className="about-container-right">
            <img alt="" className="about-container-right-img-bg" src={bgSmicu} />
            <img alt="" className="about-container-right-img" src={smicu} />
          </div>

        </div>

      <img alt="" className="about-container-chn" src={aboutChn} />
 
    </div>

  );
};

export default (About);
