import React from 'react';
import { Transition } from 'react-transition-group';
import { Link } from "react-router-dom";
import Fade from '@stahl.luke/react-reveal/Fade';

import Footer from '../Footer'

import discord from '../../assets/svg/discord.svg';
import twitter from '../../assets/svg/twitter.svg';
import opensea from '../../assets/svg/opensea.svg';

import './index.css';
import '../common-styles.css';


const durationOut = 100;

const defaultStyle = {
  transition: `opacity ${durationOut}ms ease-in-out`,
  opacity: 0,
}

const transitionStyles = {
  entering: { opacity: 1, display: 'block' },
  entered:  { opacity: 1 },
  exiting:  { opacity: 0 },
  exited:   { opacity: 0, display: 'none' },
};

const Menu = (props) => {

    return (

      <Transition in={props.in} timeout={durationOut}>

        {state => (
          
          <div style={{
            ...defaultStyle,
            ...transitionStyles[state]
          }}>

            <Fade duration={500}>

              <div className="menu-app">

                <div className="menu-container">

                  <div style={{padding: '0% 3%'}}>

                    <div className="menu-title-container" style={{marginBottom: '1vw'}}>
                      <p className="menu-title-text"> Menu </p>
                      <p className="menu-title-slash"> / </p> 
                      <p className="menu-title-description"> メニュー </p> 
                    </div>

                    <Link to="/" style={{textDecoration: "none"}} className="menu-link" onClick={props.toggleNavbar}>
                      <p className="menu-link-text"> Home </p>
                    </Link>

                    <Link to="/about" style={{textDecoration: "none"}} className="menu-link" onClick={props.toggleNavbar}>
                      <p className="menu-link-text"> About </p>
                    </Link>

                    <Link to="/manga" style={{textDecoration: "none"}} className="menu-link" onClick={props.toggleNavbar}>
                      <p className="menu-link-text"> Manga </p>
                    </Link>

                    <Link to="/roadmap" style={{textDecoration: "none"}} className="menu-link" onClick={props.toggleNavbar}>
                      <p className="menu-link-text"> Roadmap </p>
                    </Link>

                    <Link to="/updates" style={{textDecoration: "none"}} className="menu-link" onClick={props.toggleNavbar}>
                      <p className="menu-link-text"> Updates </p>
                    </Link>

                    <Link to="/team" style={{textDecoration: "none"}} className="menu-link" onClick={props.toggleNavbar}>
                      <p className="menu-link-text"> Team </p>
                    </Link>

                    <div className="menu-link-social-container">
                      <a className="menu-link-social" href="https://twitter.com/KujiraNoKonton" target="_blank" rel="noreferrer" style={{textDecoration: "none"}} >
                        <img alt="" className="menu-link-social-img" src={twitter} />
                      </a>
                      <a className="menu-link-social" href="https://discord.com/invite/kujiranokonton" target="_blank" rel="noreferrer" style={{textDecoration: "none"}} >
                        <img alt="" className="menu-link-social-img" src={discord} />
                      </a>
                      <a className="menu-link-social" href="" target="_blank" rel="noreferrer" style={{textDecoration: "none"}} >
                        <img alt="" className="menu-link-social-img" src={opensea} />
                      </a>
                    </div>

                  </div>

                  <div className="menu-next">
                    <p className="menu-next-text"> Next publication will be in </p>
                    <p className="menu-next-text" style={{color: "#DA0505"}}> soon... </p>
                  </div>

                  <Footer toggleNavbar={props.toggleNavbar} />
                  
                </div>
              </div>
            </Fade>
          </div>
        )}
      </Transition>
    );
  }


export default (Menu);